*, *::before, *::after {
    box-sizing: border-box;
}
* {
  margin: 0;
  padding: 0;
}
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}
input, button, textarea, select {
  font: inherit;
}
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

body {
  display: grid;
  height: 100vh;
  grid-template-rows: 70px 1fr 50px;
}

ul {
  list-style: none;
}

#main {
  display: grid;
  grid-template-columns: 1fr 4fr;

}

#sidebar {
  background-color: aliceblue;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 3rem;
  padding: 1rem;
}

.menu button {
  border: none;
  background-color: transparent;
  padding: 10px;
  text-align: left;
  cursor: pointer;
  width: 100%;
  font-size: 1rem;
}



.task-card {
  display: grid;
  padding: 0.1rem 1rem;
  gap: 1rem;
  /* border: 1px solid black; */
  border-radius: 5px;
  box-shadow: 1px 1px 1px 1px black;
  backdrop-filter: brightness(150%);
  grid-template-columns: 1fr auto;

}

.card-content-container {
  display: grid;
  grid-template-columns: auto 12rem 1fr auto auto auto;
  gap: 1rem;
  align-items: center;
}

.card-content-container > * {
  /* background-color: aliceblue; */
  padding: 0.5rem;
  align-content: center;
}

.card-content-container *:hover {
  background-color: aliceblue;
}

.button-container *:hover {
  background-color: aliceblue;
}
.button-container{
  display: flex;
  justify-content: center;
  align-items: center;
}

.button-container > * {
  padding: 1rem;
}
#tasks-container {
  background-color: #cbe1e2;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
}

.menu {
  margin-bottom: 1rem;
}
.menu > * {
  border-bottom: 1px solid black;
}
.menu > *:hover, 
#new-task-btn:hover,
#new-project-btn:hover {
  backdrop-filter: brightness(80%);
  
}

.project-card {
  display: grid;
  grid-template-columns: 1fr auto auto;
  align-items: center;
  align-content: center;
  padding: 0.1rem;
  cursor: pointer;
  gap: 0.1rem;
  border-bottom: 1px solid black;
}

.project-card > * {
  padding: 0.5rem 0.5rem;
}
.project-card > *:hover {
  backdrop-filter: brightness(80%);
}

#new-task-btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 0;
  cursor: pointer;
}

#new-task-btn > * {
  padding: 0.5rem;
}

#new-project-btn {
  display: flex;
  padding: 0.5rem;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

.task-completed {
  background-color: lightgreen;
  /* text-decoration: line-through; */
  color: darkgray;
}

/* #new-project-btn:hover {
  backdrop-filter: brightness(80%);
} */

dialog[open] {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  align-self: center;
  justify-self: center;
  gap: 1rem;
}

dialog ul {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
dialog ul li {
  display: flex;
  flex-direction: column;

}


header {
  background-color:cornsilk;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,oBAAoB;AACpB;IACI,sBAAsB;AAC1B;AACA;EACE,SAAS;EACT,UAAU;AACZ;AACA;EACE,gBAAgB;EAChB,mCAAmC;AACrC;AACA;EACE,cAAc;EACd,eAAe;AACjB;AACA;EACE,aAAa;AACf;AACA;EACE,yBAAyB;AAC3B;;AAEA;EACE,aAAa;EACb,aAAa;EACb,iCAAiC;AACnC;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,8BAA8B;;AAEhC;;AAEA;EACE,2BAA2B;EAC3B,aAAa;EACb,sBAAsB;EACtB,2BAA2B;EAC3B,SAAS;EACT,aAAa;AACf;;AAEA;EACE,YAAY;EACZ,6BAA6B;EAC7B,aAAa;EACb,gBAAgB;EAChB,eAAe;EACf,WAAW;EACX,eAAe;AACjB;;;;AAIA;EACE,aAAa;EACb,oBAAoB;EACpB,SAAS;EACT,6BAA6B;EAC7B,kBAAkB;EAClB,iCAAiC;EACjC,iCAAiC;EACjC,+BAA+B;;AAEjC;;AAEA;EACE,aAAa;EACb,oDAAoD;EACpD,SAAS;EACT,mBAAmB;AACrB;;AAEA;EACE,iCAAiC;EACjC,eAAe;EACf,qBAAqB;AACvB;;AAEA;EACE,2BAA2B;AAC7B;;AAEA;EACE,2BAA2B;AAC7B;AACA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,aAAa;AACf;AACA;EACE,yBAAyB;EACzB,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,aAAa;AACf;;AAEA;EACE,mBAAmB;AACrB;AACA;EACE,8BAA8B;AAChC;AACA;;;EAGE,gCAAgC;;AAElC;;AAEA;EACE,aAAa;EACb,oCAAoC;EACpC,mBAAmB;EACnB,qBAAqB;EACrB,eAAe;EACf,eAAe;EACf,WAAW;EACX,8BAA8B;AAChC;;AAEA;EACE,sBAAsB;AACxB;AACA;EACE,gCAAgC;AAClC;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,8BAA8B;EAC9B,iBAAiB;EACjB,eAAe;AACjB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,eAAe;EACf,mBAAmB;EACnB,8BAA8B;EAC9B,eAAe;AACjB;;AAEA;EACE,4BAA4B;EAC5B,mCAAmC;EACnC,eAAe;AACjB;;AAEA;;GAEG;;AAEH;EACE,aAAa;EACb,sBAAsB;EACtB,aAAa;EACb,kBAAkB;EAClB,oBAAoB;EACpB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,WAAW;AACb;AACA;EACE,aAAa;EACb,sBAAsB;;AAExB;;;AAGA;EACE,yBAAyB;AAC3B","sourcesContent":["/* basic css reset */\n*, *::before, *::after {\n    box-sizing: border-box;\n}\n* {\n  margin: 0;\n  padding: 0;\n}\nbody {\n  line-height: 1.5;\n  -webkit-font-smoothing: antialiased;\n}\nimg, picture, video, canvas, svg {\n  display: block;\n  max-width: 100%;\n}\ninput, button, textarea, select {\n  font: inherit;\n}\np, h1, h2, h3, h4, h5, h6 {\n  overflow-wrap: break-word;\n}\n\nbody {\n  display: grid;\n  height: 1